body {
  background-color: #f7fafc;
}

.circle_progress {
  width: 70px;
  height: 70px;
  position: relative;
}

#container {
  margin: 20px;
  width: 200px;
  height: 200px;
}

.hero-container {
  background-color: #cbd7e0;
  padding: 12px 24px;
  border-radius: 20px;

  @media (max-width: 991.98px) {
    padding: 12px;
  }

  @media (max-width: 767.98px) {
    border-radius: 0;
    margin: 0 -12px;
    padding: 0;
  }

  .progress-swiper-container {
    position: relative;

    .progress-swiper {
      .progressbar-text {
        font-family: "Inter", sans-serif;
        font-optical-sizing: auto;
        font-weight: bold;
      }

      .swiper-slide {
        width: auto;

        .progress-slider-card {
          background-color: #fff;
          border: 1px solid #e2e2ea;
          padding: 32px 12px 8px;
          border-radius: 16px;
          width: 240px;
          height: 180px;
          display: flex;
          flex-direction: column;
          justify-content: space-between;

          @media (max-width: 575.98px) {
            // width: auto;
            // max-width: 100%;
          }

          .col {
            padding-left: 15px;

            p {
              font-style: normal;
              font-size: 14px;
              line-height: 17px;
              letter-spacing: 0.2px;
              color: #2f2f35;
              margin-bottom: 4px;
              font-family: "Inter", sans-serif;
              font-optical-sizing: auto;
              font-weight: 600;
            }

            span.num {
              font-style: normal;
              font-weight: 700;
              font-size: 18px;
              letter-spacing: 0.1px;
              color: #0062ff;
              line-height: 1;
              font-family: "Inter", sans-serif;
              font-optical-sizing: auto;
            }

            span.text {
              font-style: normal;
              font-weight: 500;
              font-size: 13px;
              letter-spacing: 0.1px;
              color: #696974;
              line-height: 16px;
              font-family: "Inter", sans-serif;
              font-optical-sizing: auto;
              margin: 12px 0 8px;
            }
          }

          .btn-primary {
            display: flex;
            justify-content: space-between;
            align-items: center;
          }
        }
      }

      .boosts-slide {
        span.num {
          color: #ff974a !important;
        }
      }

      .hotdeal-slider {
        .circle_progress {
          display: flex;
          justify-content: center;
          align-items: center;
          border: 3px solid rgb(237, 28, 36, 0.1);
          border-radius: 50%;
        }
      }

      .wap-slide {
        span.num {
          color: #009432 !important;
        }
      }

      .voucher-slide {
        .progress-slider-card {
          max-width: 324px !important;
          padding: 14px 12px !important;
          width: 324px;

          .title {
            font-family: "Inter", sans-serif;
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: 0.2px;
            color: #2f2f35;
          }

          .voucher-content {
            background: #e3fbf0;
            border-radius: 10px;
            padding: 8px 12px;
            margin: 10px 0;

            display: flex;
            justify-content: space-between;
            align-items: center;

            .code {
              font-family: "Inter", sans-serif;
              font-style: normal;
              font-weight: 400;
              font-size: 10px;
              letter-spacing: 0.1px;
              color: #92929d;

              span {
                font-weight: 600;
              }
            }

            .price {
              font-family: "Inter", sans-serif;
              font-style: normal;
              font-weight: 500;
              font-size: 12px;
              letter-spacing: 0.1px;

              color: #696974;

              span {
                font-family: "Inter", sans-serif;
                font-style: normal;
                font-weight: 700;
                font-size: 14px;
                letter-spacing: 0.1px;
                color: #50505a;
              }
            }

            .expire {
              font-family: "Inter", sans-serif;
              font-style: normal;
              font-weight: 400;
              font-size: 10px;

              letter-spacing: 0.1px;

              color: #92929d;
            }
          }
        }
      }
    }

    .swiper-button-next,
    .swiper-button-prev {
      width: 28px;
      height: 28px;
      border-radius: 25px;
      background-color: #f7fafc;
      border: 1px solid #cbd7e0;

      &::after {
        font-size: 14px;
        color: #92929d;
      }
    }

    .swiper-button-prev {
      left: -14px;
    }

    .swiper-button-next {
      right: -14px;
    }
  }

}

.mobile-col {

  @media (max-width: 991.98px) {
    background-color: #cbd7e0;
    padding-top: 12px;
    padding-bottom: 8px;
  }
}

.mobile-progress-container {
  .item {
    background: #FFFFFF;
    border-radius: 16px;
    box-shadow: 0 0 1px 0 rgba(12, 26, 75, 0.24), 0 3px 8px -1px rgba(50, 50, 71, 0.05);
    padding: 15px 8px 8px 8px;

    .title {
      font-family: Inter-SemiBold, sans-serif;
      font-size: 13px;
      color: #2F2F35;
      margin-bottom: 14px;
      line-height: 1.2;
      min-height: 26px;
    }

    .num {
      font-family: Inter-Bold, sans-serif;
      font-size: 18px;
      color: #171725;
      letter-spacing: 0.09px;
      line-height: 1;
    }

    .label {
      font-family: Inter-Medium, sans-serif;
      font-size: 12px;
      color: #696974;
      letter-spacing: 0.1px;
      line-height: 1.2;
    }

    .progress {
      height: 6px;
      margin-top: 5px;
      margin-bottom: 12px;
    }

    .btn-primary {
      border-radius: 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      text-align: start;
      font-family: Inter-Medium, sans-serif;
      font-size: 13px;
      color: #FFFFFF;
      letter-spacing: 0.06px;
      line-height: 1.3;
    }
  }

  .item.featured {
    .progress-bar {
      background-color: #0062FF;
    }

    .progress {
      background-color: #E5EFFF;
    }

    .color {
      color: #0062FF;
    }
  }

  .item.boost {
    .color {
      color: #FF974A;
    }

    .progress-bar {
      background-color: #FF974A;
    }

    .progress {
      background-color: #FFF5ED;
    }
  }

  .item.deals {
    .title {
      margin-bottom: 8px;
    }

    .img-box {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      border: 3px solid rgba(237, 28, 36, .1);
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 11px;
    }

    .num {
      color: #ED1C24;
      margin-bottom: 3px;
    }
  }

  .item.wap {
    .color {
      color: #53BC77;
    }

    .progress-bar {
      background-color: #53BC77;
    }

    .progress {
      background-color: #E8F3E8;
    }

  }

  .item.voucher {
    padding: 14px 12px !important;

    .title {
      font-family: Inter-SemiBold, sans-serif;
      font-size: 14px;
      color: #2F2F35;
      letter-spacing: 0.2px;
      margin-bottom: 0;
      min-height: auto;
    }

    .voucher-content {
      background: #e3fbf0;
      border-radius: 10px;
      padding: 8px 12px;
      margin: 10px 0;

      display: flex;
      justify-content: space-between;
      align-items: center;

      .code {
        font-family: "Inter", sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 10px;
        letter-spacing: 0.1px;
        color: #92929d;

        span {
          font-weight: 600;
        }
      }

      .price {
        font-family: "Inter", sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        letter-spacing: 0.1px;

        color: #696974;

        span {
          font-family: "Inter", sans-serif;
          font-style: normal;
          font-weight: 700;
          font-size: 14px;
          letter-spacing: 0.1px;
          color: #50505a;
        }
      }

      .expire {
        font-family: "Inter", sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 10px;

        letter-spacing: 0.1px;

        color: #92929d;
      }
    }
  }

}


.agent-sec-col {
  @media (max-width: 767.98px) {
    background-color: #E2EAEF;
  }
}

.agent-sec-container {
  background: #ffffff;
  border-radius: 20px;
  border: 1px solid #e2e2ea;
  padding: 24px 12px;

  @media (max-width: 767.98px) {
    margin-top: 12px;
    margin-bottom: 12px;
  }


  .profile-info-sec {
    .profile-img-container {
      width: 72px;
      height: 72px;

      position: relative;

      .profile {
        border: 2px solid #d5aa55;
        border-radius: 50%;
        max-width: 100%;
        object-fit: cover;
        width: 100%;
        height: 100%;
        overflow: hidden;
      }

      .edit {
        position: absolute;
        top: 0;
        right: 0;
        cursor: pointer;
      }
    }

    .membership {
      border-radius: 16px;
      background: rgb(213, 170, 85);
      display: inline-flex;
      justify-content: center;
      align-items: center;
      padding: 1px 6px;
      margin-top: 3px;

      span {
        font-family: "Inter", sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        letter-spacing: 0.1px;
        text-transform: uppercase;

        color: #ffffff;
        margin-left: 3px;
      }
    }

    .info {
      margin-left: 20px;

      @media (max-width: 991.98px) {
        margin-left: 0;
        margin-top: 12px;
      }

      .titles {
        font-family: "Inter", sans-serif;
        font-style: normal;
        font-weight: 600;
        line-height: 120%;
        letter-spacing: 0.2px;
        color: #2f2f35;
        font-size: 20px;


        @media (max-width: 767.98px) {
          font-size: 16px;
        }

        .name {
          color: #1dbf73;
          font-size: 18px;

          @media (max-width: 767.98px) {
            font-size: 13px;
          }
        }
      }

      .desc {
        font-family: "Inter", sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.1px;

        color: #696974;
        margin-top: 6px;

        @media (max-width: 767.98px) {
          font-size: 12px;
        }
      }
    }
  }

  hr {
    margin: 14px 0 !important;
    color: #EDF2F7;
    border-color: #edf2f7;
    opacity: 1;

  }

  .score-chart-container {
    padding-top: 20px;
    background: #f7fafc;
    border-radius: 14px;
    display: flex;
    justify-content: start;
    align-items: center;
    flex-direction: column;
    position: relative;
    padding-bottom: 15px;

    .title {
      font-family: Inter-SemiBold, sans-serif;
      font-size: 18px;
      color: #2F2F35;
      letter-spacing: 0.26px;
      text-align: center;

      @media (max-width: 767.98px) {
        font-size: 14px;
      }
    }

    svg {
      max-width: 100%;
      height: auto;
    }

    .score-text {
      background: #FDCF43;
      border-radius: 10.4px;
      font-family: Inter-SemiBold, sans-serif;
      font-size: 15.6px;
      color: #000000;
      letter-spacing: 0.26px;
      padding: 6px 11px;
      position: absolute;
      bottom: 15px;
    }
  }

  .list-col {
    @media (max-width: 767.98px) {
      order: 1;
    }
  }

  .agent-score-container {

    @media (min-width: 992px) {
      margin-top: -85px;
    }

    .title {
      margin-bottom: 20px;
    }

    .score-list-sec {
      position: relative;

      .more {
        position: absolute;
        bottom: 0;
        width: 100%;
        background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.00) 0%, rgba(255, 255, 255, 0.78) 43%, #FFFFFF 100%);
        padding-top: 15px;
        text-align: center;
        display: none;
        a{
          cursor: pointer;
        }
      }

      @media (max-width: 767.98px) {
        max-height: 115px;
        overflow: hidden;
        .more {
          display: block;
        }
      }

      .score-list-item {
        display: flex;
        align-items: center;
        margin-bottom: 16px;

        .content {
          padding-left: 10px;
          padding-right: 10px;

          div {
            font-family: "Inter", sans-serif;
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            letter-spacing: 0.2px;
            color: #2f2f35;
            line-height: 1.1;
            margin-bottom: 4px;
          }

          span {
            font-family: "Inter", sans-serif;
            font-style: normal;
            font-weight: 500;
            font-size: 13px;
            letter-spacing: 0.1px;
            color: #696974;
            display: inline-block;
            line-height: 1.1;

            @media (max-width: 767.98px) {
              font-size: 12px;
            }
          }
        }

        .btn-verify {
          min-width: max-content;
        }
      }

      .score-list-item:last-child{
        margin-bottom: 0;
      }
    }

    .view-all{
      max-height: none;
      .more{
        display: none;
      }
    }
  }

  .tire-container {
    background-color: #E1EAF1;
    padding: 20px 15px 10px;
    border-radius: 14px;
    width: 100%;

    .memberships {
      .row {
        --bs-gutter-x: 6px;

        div {
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;

          .name {
            font-family: Inter-SemiBold, sans-serif;
            font-size: 13px;
            color: #000000;
            letter-spacing: 0.1px;
            text-align: center;
            line-height: 1;
            margin-bottom: 2px;
          }

          .label {
            font-family: Inter-Medium, sans-serif;
            font-size: 11px;
            color: #696974;
            letter-spacing: 0.1px;
            text-align: center;
            line-height: 1;

          }
        }
      }
    }

    .progress-container {
      position: relative;

      .progress {
        background-color: #C1D2DF;
      }

      .progress-bar {
        background-color: #1DBF73;
      }

      .check {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 20px;
        height: 20px;
        background-color: #EEF3F6;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;

        svg {
          color: #1DBE73;
          width: 16.25px;
          height: 16.25px;
          border-radius: 50%;
        }
      }

      .check-inactive {
        background-color: #C1D2DF;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }

      .active {
        background-color: #1DBF73;
      }

      .start {
        left: 15%;
        transform: translate(-16.5%, -50%);
      }

      .middle {
        left: 50%;
        transform: translate(-50%, -50%);
      }

      .end {
        left: 85%;
        transform: translate(-85%, -50%);
      }
    }

    .btn-primary{
      font-size: 13px;
      letter-spacing: 0.06px;
    }

  }
}

.update-container {
  background: #FFFFFF;
  border-radius: 20px;
  box-shadow: 0 0 1px 0 rgba(12, 26, 75, 0.24), 0 3px 8px -1px rgba(50, 50, 71, 0.05);
  padding: 22px 14px 16px;

  @media (max-width: 767.98px) {
    margin-top: 12px;
  }

  .header {
    margin-bottom: 12px;
  }

  a, a:hover {
    i {
      color: #92929D;
    }
  }

  .update-swiper {
    .swiper-slide {
      background: #E3EFFB;
      border: 1px solid #E2E2EA;
      border-radius: 20px;
      padding: 12px;

      .text {
        font-family: Inter-Medium, sans-serif;
        font-size: 14px;
        color: #000000;
        letter-spacing: 0.2px;
      }

    }

    .wap-slide {
      .content {
        margin-bottom: 15px;
        display: flex;
        gap: 12px;

        .text {
          flex: 1;
        }
      }

      .buttons {
        display: flex;
        align-items: center;
        gap: 24px;

        .btn-primary {
          flex: 1;
        }

        .btn-light {
          background-color: #FFFFFF;
          border-color: #FFFFFF;
        }
      }
    }
  }

  .update-pagination {
    margin-top: 12px;
    line-height: 6px;
    top: 0;

    .swiper-pagination-bullet {
      background: #92929D;
      border-radius: 20px;
      width: 6px;
      height: 6px;
    }

    .swiper-pagination-bullet-active {
      background: #92929D;
      border-radius: 20px;
      width: 12px;
      height: 6px;
    }
  }
}

.light-widget {
  background: #E1EAF1;
  border: 1px solid #D0D9E1;
  border-radius: 20px;
  box-shadow: 0 0 1px 0 rgba(12, 26, 75, 0.24), 0 3px 8px -1px rgba(50, 50, 71, 0.05);
  display: flex;
  padding: 16px 12px;
  gap: 12px;

  .img {
    background: #FFFFFF;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 24px;
      height: 24px;
    }
  }

  .content {
    flex: 1;
  }

  .widget-title-2 {
    margin-bottom: 2px;
  }
}

.leads-widget {
  background: #EEE7C3;
  border: 1px solid #C9C3A2;
  border-radius: 20px;
  padding: 11px 10px 14px;

  .content {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 15px 12px;
    background: #FFFADE;
    border-radius: 10px;
    margin: 11px 0;

    > diV {
      display: flex;
      flex-direction: column;
      flex: 1;
      line-height: 1;

      span {
        font-family: Inter-Medium, sans-serif;
        font-size: 12px;
        color: #92929D;
        letter-spacing: 0.1px;
        margin-bottom: 5px;
      }

      span.lead {
        font-family: Inter-Bold, sans-serif;
        font-size: 14px;
        color: #50505A;
        letter-spacing: 0.1px;
        margin-bottom: 0;
      }
    }

    i {
      width: 1px;
      min-height: 36px;
      background-color: #DED8B7;
    }

  }
}

.profile-completion {
  background: #FEE6C7;
  border-radius: 20px;
  padding: 24px 10px 20px 24px;

  svg {
    min-width: 75px;
  }

  .widget-label {
    max-width: 180px;
  }
}

.property-val {
  @extend .profile-completion;
  background: #F5EDFA;

  > div {
    gap: 12px;
  }
}

