      font-family: Inter-Medium, sans-serif;
      font-size: 16px;
      color: #000000;
      letter-spacing: 0.2px;
      line-height: 1.2;
      flex: 1;

      b {
        font-family: Inter-Bold, sans-serif;
        color: #16A05F;
      }
    }
  }
}

